import React, { useEffect, useRef } from "react";
import { gsap } from "gsap";

const CrowdSimulator = () => {
  const canvasRef = useRef(null);
  const peepsRef = useRef({
    allPeeps: [],
    availablePeeps: [],
    crowd: []
  });

  const config = {
    src: 'https://s3-us-west-2.amazonaws.com/s.cdpn.io/175711/open-peeps-sheet.png',
    rows: 15,
    cols: 7
  };

  // UTILS
  const randomRange = (min, max) => min + Math.random() * (max - min);
  const randomIndex = (array) => randomRange(0, array.length) | 0;
  const removeFromArray = (array, i) => array.splice(i, 1)[0];
  const removeItemFromArray = (array, item) => removeFromArray(array, array.indexOf(item));
  const removeRandomFromArray = (array) => removeFromArray(array, randomIndex(array));
  const getRandomFromArray = (array) => array[randomIndex(array) | 0];

  // TWEEN FACTORIES
  const resetPeep = ({ stage, peep }) => {
    const direction = Math.random() > 0.5 ? 1 : -1;
    const offsetY = 100 - 250 * gsap.parseEase('power2.in')(Math.random());
    const startY = stage.height - peep.height + offsetY;
    let startX, endX;

    if (direction === 1) {
      startX = -peep.width;
      endX = stage.width;
      peep.scaleX = 1;
    } else {
      startX = stage.width + peep.width;
      endX = 0;
      peep.scaleX = -1;
    }

    peep.x = startX;
    peep.y = startY;
    peep.anchorY = startY;

    return { startX, startY, endX };
  };

  const normalWalk = ({ peep, props }) => {
    const { startX, startY, endX } = props;
    const xDuration = 10;
    const yDuration = 0.25;

    const tl = gsap.timeline();
    tl.timeScale(randomRange(0.5, 1.5));
    tl.to(peep, {
      duration: xDuration,
      x: endX,
      ease: 'none'
    }, 0);
    tl.to(peep, {
      duration: yDuration,
      repeat: xDuration / yDuration,
      yoyo: true,
      y: startY - 10
    }, 0);

    return tl;
  };

  const walks = [normalWalk];

  // Peep Class
  class Peep {
    constructor({ image, rect }) {
      this.image = image;
      this.setRect(rect);
      this.x = 0;
      this.y = 0;
      this.anchorY = 0;
      this.scaleX = 1;
      this.walk = null;
    }

    setRect(rect) {
      this.rect = rect;
      this.width = rect[2];
      this.height = rect[3];
      this.drawArgs = [this.image, ...rect, 0, 0, this.width, this.height];
    }

    render(ctx) {
      ctx.save();
      ctx.translate(this.x, this.y);
      ctx.scale(this.scaleX, 1);
      ctx.drawImage(...this.drawArgs);
      ctx.restore();
    }
  }

  const createPeeps = (img) => {
    const { rows, cols } = config;
    const { naturalWidth: width, naturalHeight: height } = img;
    const total = rows * cols;
    const rectWidth = width / rows;
    const rectHeight = height / cols;

    for (let i = 0; i < total; i++) {
      peepsRef.current.allPeeps.push(new Peep({
        image: img,
        rect: [
          (i % rows) * rectWidth,
          (i / rows | 0) * rectHeight,
          rectWidth,
          rectHeight,
        ]
      }));
    }
  };

  const resize = (canvas, stage) => {
    stage.width = Math.ceil(canvas.clientWidth);
    stage.height = Math.ceil(canvas.clientHeight);
    canvas.width = Math.ceil(stage.width * devicePixelRatio);
    canvas.height = Math.ceil(stage.height * devicePixelRatio);

    peepsRef.current.crowd.forEach((peep) => {
      if (peep.walk) peep.walk.kill();
    });

    peepsRef.current.crowd.length = 0;
    peepsRef.current.availablePeeps.length = 0;
    peepsRef.current.availablePeeps.push(...peepsRef.current.allPeeps);

    initCrowd(stage);
  };

  const initCrowd = (stage) => {
    while (peepsRef.current.availablePeeps.length) {
      addPeepToCrowd(stage).walk.progress(Math.random());
    }
  };

  const addPeepToCrowd = (stage) => {
    const peep = removeRandomFromArray(peepsRef.current.availablePeeps);
    const walk = getRandomFromArray(walks)({
      peep,
      props: resetPeep({ peep, stage })
    }).eventCallback('onComplete', () => {
      removePeepFromCrowd(peep);
      addPeepToCrowd(stage);
    });

    peep.walk = walk;
    peepsRef.current.crowd.push(peep);
    peepsRef.current.crowd.sort((a, b) => a.anchorY - b.anchorY);

    return peep;
  };

  const removePeepFromCrowd = (peep) => {
    removeItemFromArray(peepsRef.current.crowd, peep);
    peepsRef.current.availablePeeps.push(peep);
  };

  const render = (canvas, ctx, stage) => {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.save();
    ctx.scale(devicePixelRatio, devicePixelRatio);

    peepsRef.current.crowd.forEach((peep) => {
      peep.render(ctx);
    });

    ctx.restore();
  };

  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    const stage = { width: 0, height: 0 };

    const img = new Image();
    img.onload = () => {
      createPeeps(img);
      resize(canvas, stage);
      gsap.ticker.add(() => render(canvas, ctx, stage));
    };
    img.src = config.src;

    const handleResize = () => resize(canvas, stage);
    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      gsap.ticker.remove(() => render(canvas, ctx, stage));

      // Kill all animations
      peepsRef.current.crowd.forEach(peep => {
        if (peep.walk) peep.walk.kill();
      });
    };
  }, []);

  return <canvas ref={canvasRef} className="w-full h-full" />;
};

export default CrowdSimulator;