import React, { useRef, useEffect } from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

// Prevent ScrollTrigger from refreshing on mobile address bar resize
ScrollTrigger.config({ ignoreMobileResize: true });

const TextReveal = ({ children, className = "" }) => {
    const containerRef = useRef(null);
    const textRef = useRef(null);

    useEffect(() => {
        const words = textRef.current.querySelectorAll(".word");

        const ctx = gsap.context(() => {
            const tl = gsap.timeline({
                scrollTrigger: {
                    trigger: containerRef.current,
                    start: "top top",
                    end: "+=150%", // Reduced from 200% to make it faster/more immediate
                    scrub: 0.5, // Added slight smoothing
                    pin: true,
                    // invalidateOnRefresh: true, // Ensure it recalculates correctly on actual resize
                },
            });

            // Initial state: Invisible, gray box, transparent text
            gsap.set(words, {
                opacity: 0,
                backgroundColor: "rgba(255, 255, 255, 1)", // White shade
                color: "transparent",
                borderRadius: "999px", // Roundish (pill shape)
                scale: 0.8,
            });

            // Step 1: Fade in boxes
            tl.to(words, {
                opacity: 1,
                scale: 1,
                duration: 0.5, // Faster duration
                stagger: 0.1, // Faster stagger
                ease: "power2.out",
            });

            // Step 2: Reveal text (remove box styling)
            tl.to(
                words,
                {
                    backgroundColor: "transparent",
                    color: "rgba(255, 255, 255, 0.9)",
                    borderRadius: "0px",
                    duration: 0.5, // Faster duration
                    stagger: 0.1, // Faster stagger
                    ease: "power2.in",
                },
                "<+0.2" // Start almost immediately after boxes appear
            );
        }, containerRef);

        return () => ctx.revert();
    }, [children]);

    // Split text into words for animation, handling <br /> tags and other elements
    const words = React.Children.map(children, (child, childIndex) => {
        if (typeof child === 'string') {
            return child.split(" ").map((word, wordIndex) => {
                // Skip empty strings resulting from multiple spaces to avoid empty boxes
                if (!word) return null;
                return (
                    <span key={`${childIndex}-${wordIndex}`} className="word transition-all duration-100 inline-block mx-1 px-1">
                        {word}
                    </span>
                );
            });
        }
        return child;
    });

    return (
        <div ref={containerRef} className={`relative z-10 min-h-[100dvh] flex items-center justify-center py-24 md:py-20 px-4 md:px-8 ${className}`}>
            <p ref={textRef} className="text-2xl md:text-4xl font-semibold leading-tight text-white max-w-4xl mx-auto">
                {words}
            </p>
        </div>
    );
};

export default TextReveal;
