import React, { useState } from "react";

const products = [
    { id: 1, name: "Product Alpha", image: "https://raw.githubusercontent.com/hackctl/assets/main/products/bcongen/images/bcongen.png", className: "md:rotate-[-2deg] md:top-[0%] md:left-[5%] z-[3]" },
    { id: 2, name: "Product Beta", image: "https://raw.githubusercontent.com/hackctl/assets/main/products/bman/images/bman.png", className: "md:rotate-[3deg] md:top-[15%] md:left-[35%] z-[2]" },
    { id: 3, name: "Product Gamma", image: "https://raw.githubusercontent.com/hackctl/assets/main/products/dstroman/images/dstroman.png", className: "hidden md:block md:rotate-[-1deg] md:top-[30%] md:left-[10%] z-[1]" },
    { id: 4, name: "Product Delta", image: "https://raw.githubusercontent.com/hackctl/assets/main/products/pfscgen/images/pfscgen.png", className: "md:rotate-[2deg] md:top-[10%] md:left-[60%] z-[4]" },
];

const Products = () => {
    const [hoveredProduct, setHoveredProduct] = useState(null);

    return (
        <section className="bg-black text-white py-12 md:py-24 px-4 md:px-8 overflow-hidden">
            <div className="max-w-7xl mx-auto">
                <h2 className="text-4xl md:text-7xl font-bold mb-10 md:mb-20 text-center tracking-tight text-white">
                    Products
                </h2>

                {/* Scattered Screenshots Container */}
                <div className="relative h-auto md:h-[700px] flex flex-col gap-12 md:block">
                    {products.map((product) => (
                        <div
                            key={product.id}
                            onMouseEnter={() => setHoveredProduct(product.id)}
                            onMouseLeave={() => setHoveredProduct(null)}
                            className={`relative md:absolute transition-all duration-500 ease-out hover:scale-105 hover:z-50 w-full md:w-[45%] max-w-[500px] mx-auto ${product.className}`}
                            style={{
                                opacity: hoveredProduct === null || hoveredProduct === product.id ? 1 : 0.3,
                            }}
                        >
                            <img
                                src={product.image}
                                alt={product.name}
                                className="w-full h-auto rounded-lg shadow-2xl"
                            />
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Products;
