import { HoverCard, HoverCardTrigger, HoverCardContent } from "@/components/ui/hover-card";
import { GoArrowUpRight } from "react-icons/go";
import { Mail, Menu, X } from "lucide-react";
import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { createPortal } from "react-dom";
import GeometricLogo from "@/components/GeometricLogo";

const Navbar = ({ onHoverChange }) => {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  useEffect(() => {
    if (isMobileMenuOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }
    return () => {
      document.body.style.overflow = "unset";
    };
  }, [isMobileMenuOpen]);

  const menuItems = [
    {
      label: "Services",
      to: "/services",
      subItems: [
        { label: "Platform & Hosting Operations", href: "/services#platform-hosting-operations" },
        { label: "Delivery & Automation", href: "/services#delivery-automation" },
        { label: "Containerization & Orchestration", href: "/services#containerization-orchestration" },
        { label: "Monitoring & Reliability", href: "/services#monitoring-reliability" },
      ],
    },
    {
      label: "Products",
      to: "/products",
      subItems: [
        { label: "bman", href: "/products/bman" },
        { label: "bcongen", href: "/products/bcongen" },
        { label: "dwebui", href: "/products/dwebui" },
        { label: "pfscgen", href: "/products/pfscgen" },
        { label: "prfcgen", href: "/products/prfcgen" },
      ],
    },
    {
      label: "Pricing",
      to: "/pricing",
    },
  ];

  const contactItem = {
    label: "Contact",
    to: "/contact",
  };

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
    // onHoverChange?.(!isMobileMenuOpen); // Removed to prevent blur effect on mobile
  };

  const handleMouseEnter = () => {
    if (window.innerWidth >= 768) {
      onHoverChange?.(true);
    }
  };

  const handleMouseLeave = () => {
    if (window.innerWidth >= 768) {
      onHoverChange?.(false);
    }
  };

  return (
    <header
      className="w-full px-6 md:px-12 py-4 md:py-7 flex-shrink-0"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <div className="flex items-center justify-between">
        {/* Left side: Logo + Menu */}
        <div className="flex items-center gap-10 h-full">
          <Link
            to="/"
            className={`text-3xl md:text-2xl font-semibold transition-colors cursor-pointer flex items-center h-full -translate-y-[2px] z-[100] relative ${isMobileMenuOpen ? "text-black" : "text-white hover:text-gray-300"}`}
            onClick={() => setIsMobileMenuOpen(false)}
          >
            hackctl
          </Link>

          <nav className="hidden md:flex items-center gap-6 h-full">
            {menuItems.map((item) => (
              item.subItems ? (
                <HoverCard key={item.to} openDelay={0} closeDelay={70}>
                  <HoverCardTrigger asChild>
                    <Link
                      to={item.to}
                      className="relative cursor-pointer text-white text-base font-medium group block"
                    >
                      <span className="absolute -inset-3 bg-white/10 rounded-full scale-0 transition-transform duration-200 ease-out group-hover:scale-100"></span>
                      <span className="relative z-10">{item.label}</span>
                    </Link>
                  </HoverCardTrigger>

                  <HoverCardContent
                    align="start"
                    sideOffset={20}
                    className="bg-black backdrop-blur-md border border-white/10 text-white shadow-xl w-auto min-w-max p-4"
                  >
                    <div className="flex flex-col gap-2">
                      {item.subItems.map((sub, idx) => (
                        <Link
                          key={idx}
                          to={sub.href}
                          onClick={(e) => e.stopPropagation()}
                          className="flex items-center gap-2 hover:text-gray-300 transition-colors text-sm py-1 px-2 rounded whitespace-nowrap"
                        >
                          <GoArrowUpRight className="w-4 h-4" />
                          {sub.label}
                        </Link>
                      ))}
                      <Link
                        to={item.to}
                        onClick={(e) => e.stopPropagation()}
                        className="flex items-center justify-end mt-2 text-sm font-medium text-blue-400 hover:text-blue-300 transition-colors py-1 px-2 rounded hover:bg-white/5"
                      >
                        More 
                      </Link>
                    </div>
                  </HoverCardContent>
                </HoverCard>
              ) : (
                <Link
                  key={item.to}
                  to={item.to}
                  className="relative cursor-pointer text-white text-base font-medium group"
                >
                  <span className="absolute -inset-3 bg-white/10 rounded-full scale-0 transition-transform duration-200 ease-out group-hover:scale-100"></span>
                  <span className="relative z-10">{item.label}</span>
                </Link>
              )
            ))}
          </nav>
        </div>

        {/* Right side: Contact (Desktop) + Mobile Toggle */}
        <div className="flex items-center h-full gap-4">
          <div className="hidden md:flex items-center h-full">
            <Link
              to={contactItem.to}
              className="relative cursor-pointer text-white text-base group flex items-center gap-1.5 hover:opacity-80 transition-opacity"
            >
              <span className="absolute -inset-3 bg-white/10 rounded-full scale-0 transition-transform duration-200 ease-out group-hover:scale-100"></span>
              <Mail className="w-5 h-5 relative z-10" />
              <span className="relative z-10">{contactItem.label}</span>
            </Link>
          </div>

          {/* Mobile Menu Button */}
          <button
            className="md:hidden text-white z-50 relative p-2"
            onClick={toggleMobileMenu}
            aria-label="Toggle mobile menu"
          >
            <Menu className="w-8 h-8" />
          </button>
        </div>

        {/* Mobile Menu Overlay - Portaled to body */}
        {isMobileMenuOpen && createPortal(
          <div className="fixed inset-0 bg-black z-[9999] flex flex-col md:hidden overflow-y-auto">
            <div className="flex justify-between items-center p-6 md:px-12 py-4 md:py-7">
              <GeometricLogo className="w-12 h-12 text-white" />
              <button
                className="text-white p-2"
                onClick={() => setIsMobileMenuOpen(false)}
                aria-label="Close mobile menu"
              >
                <X className="w-8 h-8" />
              </button>
            </div>

            <nav className="flex flex-col gap-8 px-6 pb-12 mt-4 items-center justify-center flex-1 -mt-24">
              {menuItems.map((item) => (
                <div key={item.to} className="flex flex-col gap-4">
                  <Link
                    to={item.to}
                    className="text-2xl font-semibold text-white hover:text-gray-300"
                    onClick={() => setIsMobileMenuOpen(false)}
                  >
                    {item.label}
                  </Link>
                </div>
              ))}
              <div className="flex flex-col gap-4">
                <Link
                  to={contactItem.to}
                  className="text-2xl font-semibold text-white hover:text-gray-300"
                  onClick={() => setIsMobileMenuOpen(false)}
                >
                  {contactItem.label}
                </Link>
              </div>
            </nav>
          </div>,
          document.body
        )}
      </div>
    </header>
  );
};

export default Navbar;
